@import url(https://fonts.googleapis.com/css?family=Muli|Ovo);
/* font-family: 'Muli', sans-serif;
font-family: 'Ovo', serif; */

:root {
    --ovo: 'Muli', sans-serif;
    --muli: 'Muli', sans-serif;
    --color1: #8EC9D2;
    --color2: #B7B8BF;
    --color3: #2E334A;
}

html, body {
    margin: 0;
    padding: 0;
    background: #2E334A;
}

.row {
    margin-left: 0;
    margin-right: 0;
}

.swal-game-over-text{
    font-family: 'Muli', sans-serif;
    font-family: var(--muli);
    font-size: 4vh;
    color: black;
    text-shadow: 1.5px 1.5px darkred;
}

.swal-success-text{
    font-family: 'Muli', sans-serif;
    font-family: var(--muli);
    font-size: 4vh;
    color: black;
    text-shadow: 1.5px 1.5px green;
}

.swal-modal {
    background: rgb(255, 255, 255, 0.8);
    border: 3px solid #B7B8BF;
    border: 3px solid var(--color2);
}

.swal-overlay {
  background-color: rgb(0, 0, 0, 0.9);
}
.jumbotron-bg{
    background: rgb(255, 255, 255, 0.6)
}

.jumbotron-title-text{
    font-family: var(--ovo);
    font-size: 6.5vh;
    color: darkgoldenrod;
    text-shadow: 2px 2px black;
}

.jumbotron-lead-text{
    font-family: var(--muli);
    font-size: 3vh;
    font-weight: bold;
    color: darkgoldenrod;
    text-shadow: 1px 1px black;
}
.score-divider {
    border-top: 4px solid var(--color2);
    border-bottom: 1px solid darkcyan;
}

.score-section-instruction-text, .score-section-showscore-text{
    color: var(--color1);
    font-family: var(--moli);
    font-size: 3vh;
    text-shadow: 0.5px 0.5px darkgoldenrod;
}
.clicky-image{
    width: 80%;
    height: 25vh;
    margin: 10%;
    cursor: pointer;
}

.clicky-image:hover, .clicky-image:active, .clicky-image:focus {
    border: 5px double whitesmoke;
    border-radius: 15px;
}
